	color: #fff;
	font-size: 24px;
	font-style: normal;
	font-weight: 400;
	line-height: 32px;
	background-color: #7ac100;
	align-items: center;
	flex-wrap: wrap;
	justify-content: space-around;
	border: none;
	border-radius: 12px;
}

button.start:disabled {
	background-color: #757a6d;
}

.header {
	row-gap: 23px;
